.unordered_list_Of_Colors {
	display: flex;
	gap: 10px;
	flex-wrap: wrap;
}
.list_of_colors {
	/* border: 4px solid red; */
	width: 200px;
	border-radius: 10px;
	overflow: hidden;
}

colorText {
}

.first_color_item {
	height: 100px;
	/* cursor: pointer; */
}

.second_color_item {
	height: 70px;
}

.third_color_item {
	height: 40px;
}
.fourth_color_item {
	height: 30px;
}

first_color_item:hover {
	cursor: pointer;
	scale: 1;
}

.color_likes {
	cursor: pointer;
	margin-left: 10px;
	margin-top: 20px;
	margin-bottom: 10px;
	border: 1px solid lightgray;
	border-radius: 16px;
	width: 70px;
	padding: 5px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.number_likes {
	font-size: 20px;
}

.material-symbols-outlined {
	color: rgba(56, 56, 56, 0.905);
	font-variation-settings: 'FILL' 0, 'wght' 200, 'GRAD' 200, 'opsz' 48;
}
